.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.MyForm {
  display: flex;
  flex-direction: column;
  padding: 5px 10px;
}
.table {
  display: flex;
  flex-direction: column-reverse;
  border: solid 2px #282c34;
  margin: 10px;
}
.row {
  display: flex;
  margin: 0px -2px;
  border-bottom: solid 2px #282c34;
  flex-direction: row;
  justify-content: space-around;
}
.row:first-child {
  border-bottom: none;
}
.column {
  flex-grow: 2;
}
.byn {
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: 100px;
}
.usd {
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: 100px;
}
.buy {
  color: #4CAF50;
}
.not-buy {
  color: #f44336;
}
.buy:hover {
  color: #ffffff;
  background-color: #4CAF50;
}
.not-buy:hover {
  color: #ffffff;
  background-color: #f44336;
}
input {
  margin: 5px 0px;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
